.js-to_expand[aria-hidden=true],
.js-to_expand[data-hidden=true] {
  display: none;
}

.expandmore__button {
  background: transparent;
  font-size: inherit;
  color: inherit;
  border: 0;

  &:focus {
    outline: thin dotted;
    border-color: $border-color-collapse-focus;
  }
}

// // Exemple de règles pour visualiser la fonctionalité
// // plier /déplier

.expand-sign:before,
.expand-sign:before {
  content : $expand-sign-closed;
}

.expandmore__button[aria-expanded=true] .expand-sign:before ,
.expandmore__button[data-expanded=true] .expand-sign:before {
  content : $expand-sign-open;
}

.no-js .js-expandmore-all {
  display: none;
}
